"use client";

import { useEffect, useState } from "react";

type Props = {
    isPlaying: boolean;
    /** „ÄåÂÜçÁîüÈñãÂßãÊôÇ„Å´WiiÊé•Á∂ö„Åó„Å¶„ÅÑ„Åü„Äç„Éï„É©„Ç∞ */
    startedWithWii: boolean;
    /** ÁèæÂú®„ÅÆÊé•Á∂öÁä∂ÊÖãÔºàË°®Á§∫ÊñáË®Ä„Å™„Å©„Å´‰Ωø„ÅÜ„Å™„ÇâÊÆã„Åó„Å¶OKÔºâ */
    wiiConnected: boolean;

    /** backend„Åã„ÇâÊù•„ÅüÂàáÊñ≠„Ç§„Éô„É≥„ÉàÔºàÊõ¥Êñ∞„Åï„Çå„Çã„Åü„Å≥„Å´„Éù„ÉÉ„Éó„Ç¢„ÉÉ„Éó„ÇíÈñã„ÅèÔºâ */
    wiiDisconnectedAt: number | null;

    /** ‚òÖËøΩÂä†: ÂÜçÁîüÈñãÂßãÊôÇÂàªÔºà„Åì„Çå‰ª•Èôç„ÅÆÂàáÊñ≠„ÅÆ„Åø„Éù„ÉÉ„Éó„Ç¢„ÉÉ„ÉóÂØæË±°„Å´„Åô„ÇãÔºâ */
    playingSince: number;
};

export function WiiDisconnectPopup({
    isPlaying,
    startedWithWii,
    wiiConnected,
    wiiDisconnectedAt,
    playingSince,
}: Props) {
    const [open, setOpen] = useState(false);

    // ‚òÖÂ§âÊõ¥: „ÄåÁô∫Ë°®‰∏≠„Äç„Åã„Å§„ÄåÈñãÂßãÊôÇ„Å´Êé•Á∂ö„Åó„Å¶„ÅÑ„Åü„ÄçÂ†¥Âêà„ÅÆ„Åø„ÄÅÂàáÊñ≠„Ç§„Éô„É≥„Éà„ÅßÈñã„Åè
    useEffect(() => {
        if (!isPlaying) return;
        if (!startedWithWii) return;
        if (!wiiDisconnectedAt) return;
        // ‚òÖËøΩÂä†: ÂÜçÁîüÈñãÂßãÂæå„Å´Ëµ∑„Åç„ÅüÂàáÊñ≠„Å†„Åë„ÇíÂØæË±°„Å´„Åô„ÇãÔºàÈñãÂßãÂâç„ÅÆÂàáÊñ≠„Ç§„Éô„É≥„Éà„ÇíÁÑ°Ë¶ñÔºâ
        if (wiiDisconnectedAt < playingSince) return;
        setOpen(true);
    }, [isPlaying, startedWithWii, wiiDisconnectedAt, playingSince]);

    if (!open) return null;

    return (
        <div
            role="dialog"
            aria-modal="true"
            aria-label="Wii„É™„É¢„Ç≥„É≥„ÅÆÊé•Á∂ö„ÅåÂàá„Çå„Åæ„Åó„Åü"
            style={{
                position: "absolute",
                inset: 0,
                zIndex: 30000,
                display: "grid",
                placeItems: "center",
                background: "rgba(0,0,0,0.75)",
                color: "white",
                padding: 24,
            }}
            onClick={() => setOpen(false)}
        >
            <div
                style={{
                    width: "min(720px, 92vw)",
                    borderRadius: 14,
                    background: "rgba(15,15,15,0.95)",
                    border: "1px solid rgba(255,255,255,0.15)",
                    boxShadow: "0 20px 80px rgba(0,0,0,0.6)",
                    padding: 24,
                    textAlign: "center",
                }}
                onClick={(e) => e.stopPropagation()}
            >
                <div style={{ fontSize: 30, fontWeight: 800, marginBottom: 10 }}>
                    Wii„É™„É¢„Ç≥„É≥„ÅÆÊé•Á∂ö„ÅåÂàá„Çå„Åæ„Åó„Åü
                </div>
                <div style={{ fontSize: 16, opacity: 0.9, lineHeight: 1.6 }}>
                    Êé•Á∂öÔºàBluetooth/ÈõªÊ±†Ôºâ„ÇíÁ¢∫Ë™ç„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ<br />
                    „Ç≠„Éº„Éú„Éº„ÉâÊìç‰ΩúÔºà‚Üê/‚ÜíÔºâ„ÅØÂºï„ÅçÁ∂ö„ÅçÂà©Áî®„Åß„Åç„Åæ„Åô„ÄÇ
                </div>

                <div style={{ marginTop: 16, display: "flex", justifyContent: "center", gap: 12 }}>
                    <button onClick={() => setOpen(false)} style={{ padding: "10px 16px", fontSize: 16 }}>
                        Èñâ„Åò„Çã
                    </button>
                </div>
            </div>
        </div>
    );
}