function PdfSlide(props: {
	assetId: string;
	page: number;
	fallbackDataUrl?: string;
	alt: string;
	getOrLoadPdfDocument: (assetId: string) => Promise<any>;
}) {
	const { assetId, page, fallbackDataUrl, alt, getOrLoadPdfDocument } = props;
	const wrapperRef = useRef<HTMLDivElement | null>(null);
	const canvasRef = useRef<HTMLCanvasElement | null>(null);
	const [size, setSize] = useState<{ w: number; h: number } | null>(null);
	const [renderError, setRenderError] = useState<string | null>(null);
	const renderTaskRef = useRef<any>(null); // ‚òÖËøΩÂä†: „É¨„É≥„ÉÄ„É™„É≥„Ç∞„Çø„Çπ„ÇØ„ÅÆÂèÇÁÖß„Çí‰øùÊåÅ

	useEffect(() => {
		const el = wrapperRef.current;
		if (!el) return;
		const update = () => {
			const rect = el.getBoundingClientRect();
			setSize({ w: Math.max(0, rect.width), h: Math.max(0, rect.height) });
		};
		update();
		const ro = new ResizeObserver(() => update());
		ro.observe(el);
		return () => ro.disconnect();
	}, []);

	useEffect(() => {
		let cancelled = false;

		(async () => {
			try {
				setRenderError(null);

				const el = wrapperRef.current;
				const canvas = canvasRef.current;
				if (!el || !canvas || !size || size.w === 0 || size.h === 0) return;

				// ‚òÖËøΩÂä†: Êó¢Â≠ò„ÅÆ„É¨„É≥„ÉÄ„É™„É≥„Ç∞„Çí„Ç≠„É£„É≥„Çª„É´
				if (renderTaskRef.current) {
					try {
						renderTaskRef.current.cancel();
					} catch (e) {
						// „Ç≠„É£„É≥„Çª„É´Ê∏à„Åø„ÅÆÂ†¥Âêà„ÅØÁÑ°Ë¶ñ
					}
					renderTaskRef.current = null;
				}

				const pdf = await getOrLoadPdfDocument(assetId);
				if (cancelled) return;
				const pdfPage = await pdf.getPage(page);
				if (cancelled) return;

				const viewport1 = pdfPage.getViewport({ scale: 1 });
				const scale = Math.min(size.w / viewport1.width, size.h / viewport1.height);
				const dpr = typeof window !== "undefined" ? window.devicePixelRatio || 1 : 1;
				const renderViewport = pdfPage.getViewport({ scale: scale * dpr });

				canvas.width = Math.floor(renderViewport.width);
				canvas.height = Math.floor(renderViewport.height);
				canvas.style.width = `${Math.floor(renderViewport.width / dpr)}px`;
				canvas.style.height = `${Math.floor(renderViewport.height / dpr)}px`;

				const ctx = canvas.getContext("2d");
				if (!ctx) return;

				ctx.setTransform(1, 0, 0, 1, 0, 0);
				ctx.clearRect(0, 0, canvas.width, canvas.height);

				// ‚òÖ‰øÆÊ≠£: render„Çø„Çπ„ÇØ„Çí‰øùÊåÅ„Åó„ÄÅcleanup„Åßcancel„Åß„Åç„Çã„Çà„ÅÜ„Å´„Åô„Çã
				const task = pdfPage.render({ canvasContext: ctx, canvas, viewport: renderViewport });
				renderTaskRef.current = task;

				await task.promise;

				// ÂÆå‰∫Ü„Åó„Åü„ÇâÂèÇÁÖß„ÇíÂ§ñ„Åô
				if (renderTaskRef.current === task) {
					renderTaskRef.current = null;
				}
			} catch (e: any) {
				// cancel„ÅØÊ≠£Â∏∏Á≥ª„Å®„Åó„Å¶ÁÑ°Ë¶ñ
				const msg = e?.name === "RenderingCancelledException" ? null : (e instanceof Error ? e.message : String(e));
				if (!cancelled && msg) setRenderError(msg);
			}
		})();

		return () => {
			cancelled = true;
			// ‚òÖËøΩÂä†: effect cleanup„ÅßÈÄ≤Ë°å‰∏≠render„ÇíÂøÖ„ÅöÊ≠¢„ÇÅ„Çã
			if (renderTaskRef.current) {
				try {
					renderTaskRef.current.cancel();
					renderTaskRef.current = null;
				} catch {
					// Êó¢„Å´„Ç≠„É£„É≥„Çª„É´Ê∏à„Åø„ÅÆÂ†¥Âêà„ÅØÁÑ°Ë¶ñ
				}
			}
		};
	}, [assetId, getOrLoadPdfDocument, page, size]);

	return (
		<div
			ref={wrapperRef}
			style={{ width: "100%", height: "100%", display: "flex", alignItems: "center", justifyContent: "center" }}
		>
			{renderError && fallbackDataUrl ? (
				<img src={fallbackDataUrl} style={{ width: "100%", height: "100%", objectFit: "contain" }} alt={alt} />
			) : (
				<canvas ref={canvasRef} aria-label={alt} />
			)}
		</div>
	);
}

function VideoSlide(props: { assetId: string; alt: string }) {
	const { assetId, alt } = props;
	const [src, setSrc] = useState<string | null>(null);
	const [error, setError] = useState<string | null>(null);

	useEffect(() => {
		let active = true;
		let url: string | null = null;
		(async () => {
			try {
				setError(null);
				setSrc(null);
				const blob = await getAssetBlob(assetId);
				if (!blob) throw new Error("ÂãïÁîª„Ç¢„Çª„ÉÉ„Éà„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì (IndexedDB)");
				url = URL.createObjectURL(blob);
				if (!active) return;
				setSrc(url);
			} catch (e) {
				if (!active) return;
				setError(e instanceof Error ? e.message : String(e));
			}
		})();
		return () => {
			active = false;
			if (url) URL.revokeObjectURL(url);
		};
	}, [assetId]);

	if (error) {
		return <div style={{ color: "white", textAlign: "center" }}>ÂãïÁîª„ÅÆË™≠„ÅøËæº„Åø„Å´Â§±Êïó„Åó„Åæ„Åó„Åü: {error}</div>;
	}
	if (!src) {
		return <div style={{ color: "white", textAlign: "center" }}>ÂãïÁîª„ÇíË™≠„ÅøËæº„Åø‰∏≠...</div>;
	}

	return (
		<video
			src={src}
			style={{ width: "100%", height: "100%", objectFit: "contain" }}
			controls
			autoPlay
			muted
			playsInline
			aria-label={alt}
		/>
	);
}

// IR„Ç´„É°„É©„ÅÆÂ∫ßÊ®ô(0-1023)„ÇíÁîªÈù¢Â∫ßÊ®ô„Å´Â§âÊèõ„Åô„ÇãÈñ¢Êï∞
function mapIrToScreen(irX: number, irY: number, screenW: number, screenH: number) {
	// Wii„É™„É¢„Ç≥„É≥„ÅÆIR„ÅØË¶ñÁÇπ„ÅåÈÄÜ„Å´„Å™„Çã„Åì„Å®„Åå„ÅÇ„Çã„Åü„ÇÅ„ÄÅÂøÖË¶Å„Å´Âøú„Åò„Å¶ 1 - ... „ÇíË™øÊï¥„Åó„Å¶„Åè„Å†„Åï„ÅÑ
	const x = (1 - irX / 1024) * screenW;
	const y = (irY / 768) * screenH;
	return { x, y };
}

export function PresenterView() {
    const router = useRouter();
    const searchParams = useSearchParams();
    const containerRef = useRef<HTMLDivElement | null>(null);
    const isMouseDrawingRef = useRef(false);

    const soundboardRef = useRef<{ q?: HTMLAudioElement; w?: HTMLAudioElement; e?: HTMLAudioElement }>({});
    const playSound = useCallback((key: "q" | "w" | "e") => {
        const a = soundboardRef.current[key];
        if (!a) return;
        try {
            a.currentTime = 0;
            void a.play();
        } catch (err) {
            console.warn("sound play failed", key, err);
        }
    }, []);

    const returnTo = useMemo(() => {
        return searchParams.get("from") === "editor" ? "/editor" : "/";
    }, [searchParams]);

    const returnLabel = useMemo(() => {
        return returnTo === "/editor" ? "„Ç®„Éá„Ç£„Çø„Å´Êàª„Çã" : "„Éõ„Éº„É†„Å´Êàª„Çã";
    }, [returnTo]);

    const goBack = useCallback(() => {
        router.push(returnTo);
    }, [router, returnTo]);

    // Wii„É™„É¢„Ç≥„É≥„ÅÆÁä∂ÊÖã„ÇíÂèñÂæó
    const { wiiState, pressed, wiiConnected, wiiDisconnectedAt } = useWiiController();

    const [flow, setFlow] = useState<SerializedFlow | null>(null);
    const [error, setError] = useState<string | null>(null);
    const [currentNodeId, setCurrentNodeId] = useState<string | null>(null);
    const [startedWithWii, setStartedWithWii] = useState(false);
    const [playingSince, setPlayingSince] = useState<number>(0);

    const pdfDocCacheRef = useRef<Map<string, Promise<any>>>(new Map());

    // ‚òÖ‰øÆÊ≠£: Â∏∏„Å´playingÁä∂ÊÖã„Å®„Åó„Å¶Êâ±„ÅÜÔºàflow/currentNodeId„Åå„ÅÇ„Çå„Å∞ÂÜçÁîü‰∏≠Ôºâ
    const isPlaying = flow != null && currentNodeId != null;

    useEffect(() => {
		const q = new Audio("https://www.myinstants.com/media/sounds/nice-shot-wii-sports_DJJ0VOz.mp3");
		const w = new Audio("https://www.myinstants.com/media/sounds/crowdaw.mp3");
		const e = new Audio("https://www.myinstants.com/media/sounds/crowdoh.mp3");
		q.preload = "auto";
		w.preload = "auto";
		e.preload = "auto";
		soundboardRef.current = { q, w, e };
		return () => {
			for (const a of [q, w, e]) {
				try {
					a.pause();
				} catch {
					// ignore
				}
			}
			soundboardRef.current = {};
		};
	}, []);

    const getOrLoadPdfDocument = useCallback(async (assetId: string) => {
        const cached = pdfDocCacheRef.current.get(assetId);
        if (cached) return await cached;

        const promise = (async () => {
            const blob = await getAssetBlob(assetId);
            if (!blob) throw new Error("PDF„Ç¢„Çª„ÉÉ„Éà„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì (IndexedDB)");
            const arrayBuffer = await blob.arrayBuffer();
            const pdfjs = await import("pdfjs-dist/legacy/build/pdf.mjs");
            pdfjs.GlobalWorkerOptions.workerSrc = new URL(
				"pdfjs-dist/legacy/build/pdf.worker.min.mjs",
				import.meta.url,
			).toString();
            return await pdfjs.getDocument({ data: arrayBuffer }).promise;
        })();

        pdfDocCacheRef.current.set(assetId, promise);
        try {
            return await promise;
        } catch (e) {
            pdfDocCacheRef.current.delete(assetId);
            throw e;
        }
    }, []);

    // „ÅäÁµµÊèè„ÅçÁî®„ÅÆÂ∫ßÊ®ô„É™„Çπ„Éà
    const [drawingPoints, setDrawingPoints] = useState<Array<{ x: number; y: number } | null>>([]);
    const canvasRef = useRef<HTMLCanvasElement>(null);
    const wasWiiADownRef = useRef(false);

    // ÈÄ£Á∂öÈÅ∑Áßª„ÇíÈò≤„Åê„Åü„ÇÅ„ÅÆ„ÇØ„Éº„É´„Çø„Ç§„É†ÁÆ°ÁêÜ
    const lastNavTime = useRef<number>(0);

    // ÁèæÂú®„ÅÆ„Éé„Éº„Éâ„Éá„Éº„Çø
    const currentNode = useMemo(() =>
		flow?.nodes.find((n) => n.id === currentNodeId),
		[flow, currentNodeId]);

    // ‚òÖ‰øÆÊ≠£: ÂàùÂõû„Éû„Ç¶„É≥„ÉàÊôÇ„Å´Ëá™ÂãïÁöÑ„Å´„Éó„É¨„Çº„É≥„ÉÜ„Éº„Ç∑„Éß„É≥„ÇíÈñãÂßã
    useEffect(() => {
        const loaded = loadFromLocalStorage();
        if (!loaded || loaded.nodes.length === 0) {
            setError("„Éá„Éº„Çø„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì„ÄÇEditor„Åß‰ΩúÊàê„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ");
            setFlow(null);
            setCurrentNodeId(null);
            return;
        }
        
        // „Éê„Ç§„É≥„ÉâË®≠ÂÆö„ÇíË™≠„ÅøËæº„Åø
        const storedBindings = getProjectBindings();
        const flowWithBindings = storedBindings ? 
            { ...loaded, projectBindings: storedBindings } : loaded;
        
        console.log("PresenterView: Auto-starting presentation with bindings", { storedBindings, flowWithBindings });
        
        setError(null);
        setFlow(flowWithBindings);
        
        // Start„Éé„Éº„Éâ„Åã„ÇâÈñãÂßã
        const startNode = loaded.nodes.find((n) => n.data.label === "Start") || loaded.nodes[0];
        setCurrentNodeId(startNode.id);
        
        // WiiÊé•Á∂öÁä∂ÊÖã„ÇíË®òÈå≤
        setStartedWithWii(!!wiiConnected);
        setPlayingSince(Date.now());
    }, []); // ‚òÖÁ©∫„ÅÆ‰æùÂ≠òÈÖçÂàó„ÅßÂàùÂõû„ÅÆ„ÅøÂÆüË°å

    // ‚òÖ‰øÆÊ≠£: wiiConnected„ÅåÂ§âÂåñ„Åó„Åü„ÇâË®òÈå≤„ÇíÊõ¥Êñ∞
    useEffect(() => {
        if (wiiConnected) setStartedWithWii(true);
    }, [wiiConnected]);

    const outgoingEdges = useMemo(() => {
        if (!flow || !currentNodeId) return [];
        return flow.edges.filter((e) => e.source === currentNodeId);
    }, [flow, currentNodeId]);

    const branchOptions = useMemo(() => {
        // 1-9 „ÅÆÊï∞Â≠ó„ÅßÈÅ∏„Åπ„ÇãÂàÜÂ≤ê
		const options: Array<{ key: string; target: string }> = [];
		const used = new Set<string>();

		for (const edge of outgoingEdges) {
			const label = (edge.label ?? "").trim();
			const m = label.match(/^([1-9])(?:\b|\s|:|-)/);
			if (m) {
				const k = m[1];
				if (!used.has(k)) {
					options.push({ key: k, target: edge.target });
					used.add(k);
				}
			}
		}

		// „É©„Éô„É´„Å´Áï™Âè∑„Åå„Å™„ÅÑÂ†¥Âêà„ÅØ„ÄÅÈÖçÂàóÈ†Ü„Åß 1..n „ÇíÂâ≤„ÇäÂΩì„Å¶
		for (const edge of outgoingEdges) {
			if (options.length >= 9) break;
			const nextKey = String(options.length + 1);
			if (used.has(nextKey)) continue;
			options.push({ key: nextKey, target: edge.target });
			used.add(nextKey);
		}

		return options;
    }, [outgoingEdges]);

    const hasMultipleBranches = outgoingEdges.length >= 2;

    // „Éé„Éº„ÉâÁßªÂãïÂá¶ÁêÜ
    const navigateTo = useCallback((nodeId: string) => {
        // „ÇØ„Éº„É´„Çø„Ç§„É†„ÉÅ„Çß„ÉÉ„ÇØ (500ms‰ª•ÂÜÖ„ÅÆÈÄ£Á∂öÈÅ∑Áßª„ÅØÁÑ°Ë¶ñ)
		const now = Date.now();
		if (now - lastNavTime.current < 500) return;
		lastNavTime.current = now;

		setCurrentNodeId(nodeId);
		setDrawingPoints([]); // „Çπ„É©„Ç§„Éâ„ÅåÂ§â„Çè„Å£„Åü„ÇâÁ∑ö„ÇíÊ∂à„Åô
    }, []);

    // Ê¨°„Å∏Ôºà„É≠„Ç∏„ÉÉ„ÇØÊîπËâØÁâàÔºâ
    const nextSlide = useCallback(() => {
        if (!flow || !currentNodeId) return;
		// ÂàÜÂ≤ê„ÅåË§áÊï∞„ÅÇ„ÇãÂ†¥Âêà„ÅØ„ÄÅÊï∞Â≠óÈÅ∏Êäû„ÇíÂÑ™ÂÖà„Åô„Çã
		const edges = flow.edges.filter((e) => e.source === currentNodeId);
		if (edges.length >= 2) return;

		// ÁèæÂú®„ÅÆ„Éé„Éº„Éâ„Åã„ÇâÂá∫„Å¶„ÅÑ„Çã„Ç®„ÉÉ„Ç∏„Çí„Åô„Åπ„Å¶ÂèñÂæó
		// (‰∏ä„ÅßÂèñÂæóÊ∏à„Åø)

		if (edges.length === 0) return;

		// ÂÑ™ÂÖàÈ†Ü‰Ωç‰ªò„Åë
		// 1. „É©„Éô„É´„Åå„Å™„ÅÑ„Ç®„ÉÉ„Ç∏ („Éá„Éï„Ç©„É´„Éà„É´„Éº„Éà)
		// 2. „É©„Éô„É´„Åå "next" „ÅÆ„Ç®„ÉÉ„Ç∏
		// 3. „Åù„Çå‰ª•Â§ñ (ÊúÄÂàù„Å´Ë¶ã„Å§„Åã„Å£„Åü„ÇÇ„ÅÆ)
		const targetEdge =
			edges.find(e => !e.label || e.label.trim() === "") ||
			edges.find(e => e.label === "next") ||
			edges[0];

		if (targetEdge) navigateTo(targetEdge.target);
    }, [flow, currentNodeId, navigateTo]);

    const branchByNumberKey = useCallback(
		(key: string) => {
			if (!hasMultipleBranches) return;
			const opt = branchOptions.find((o) => o.key === key);
			if (opt) navigateTo(opt.target);
		},
		[branchOptions, hasMultipleBranches, navigateTo],
	);

    // Ââç„Å∏ÔºàÈÄÜÈ†ÜÊ§úÁ¥¢Ôºâ
    const prevSlide = useCallback(() => {
        if (!flow || !currentNodeId) return;
		// Ëá™ÂàÜ„Å´Âêë„Åã„Å£„Å¶„ÅÑ„Çã„Ç®„ÉÉ„Ç∏„ÇíÊé¢„Åó„Å¶Êàª„ÇãÔºàÁ∞°ÊòìÂÆüË£ÖÔºâ
		const edge = flow.edges.find(e => e.target === currentNodeId);
		if (edge) navigateTo(edge.source);
    }, [flow, currentNodeId, navigateTo]);

    // ÂàÜÂ≤êÂá¶ÁêÜÔºà„Ç®„ÉÉ„Ç∏„ÅÆ„É©„Éô„É´„ÅßÊ§úÁ¥¢Ôºâ
    const branchTo = useCallback((keywords: string[]) => {
        if (!flow || !currentNodeId) return;
		const edges = flow.edges.filter(e => e.source === currentNodeId);
		const target = edges.find(e => keywords.some(k => e.label?.includes(k)));
		if (target) {
			console.log("ÂàÜÂ≤ê„Åó„Åæ„Åó„Åü:", target.label);
			navigateTo(target.target);
		}
    }, [flow, currentNodeId, navigateTo]);

    // ‚òÖËøΩÂä†: „É™„Ç¢„ÇØ„Ç∑„Éß„É≥„Çí„Éá„Éê„ÉÉ„Ç∞„Åô„ÇãÔºàN=One, M=TwoÔºâ
    const [debugEmitClap, setDebugEmitClap] = useState(false);
    const [debugEmitLaugh, setDebugEmitLaugh] = useState(false);

    // „Ç≠„Éº„Éú„Éº„ÉâÊìç‰Ωú (Áü¢Âç∞„Ç≠„ÉºÂØæÂøú + ESC„ÅßÊàª„Çã)
    useEffect(() => {
        if (!isPlaying) return;

        const handleKeyDown = (e: KeyboardEvent) => {
            // ËøΩÂä†: Á∑ö„Çí„ÇØ„É™„Ç¢ (R)
			if (e.key === "r" || e.key === "R") {
				setDrawingPoints([]);
				isMouseDrawingRef.current = false;
				wasWiiADownRef.current = false;
				return;
			}
			
			if (!e.repeat) {
				if (e.key === "q" || e.key === "Q") {
					playSound("q");
					return;
				}
				if (e.key === "w" || e.key === "W") {
					playSound("w");
					return;
				}
				if (e.key === "e" || e.key === "E") {
					playSound("e");
					return;
				}
			}

			// ‚òÖËøΩÂä†: „É™„Ç¢„ÇØ„Ç∑„Éß„É≥ÔºàN / MÔºâ
			// Êäº„Åó„Å£„Å±„Å™„Åó„ÅßÂ¢óÊÆñ„Åó„Å™„ÅÑ„Çà„ÅÜ„Å´ repeat „ÇíÁÑ°Ë¶ñ
			if (!e.repeat) {
				if (e.key === "n" || e.key === "N") {
					setDebugEmitClap(true);
					queueMicrotask(() => setDebugEmitClap(false)); // 1Âõû„Å†„ÅëÁô∫ÁÅ´
					return;
				}
				if (e.key === "m" || e.key === "M") {
					setDebugEmitLaugh(true);
					queueMicrotask(() => setDebugEmitLaugh(false)); // 1Âõû„Å†„ÅëÁô∫ÁÅ´
					return;
				}
			}

			// Êó¢Â≠ò: ÂàÜÂ≤ê 1..9
			if (e.key >= "1" && e.key <= "9") {
				branchByNumberKey(e.key);
				return;
			}
			if (e.key === "ArrowRight") {
				if (!hasMultipleBranches) nextSlide();
			}
			if (e.key === "ArrowLeft") prevSlide();
			// ESC„Ç≠„Éº„ÅßÂÖÉ„ÅÆÁîªÈù¢„Å∏Êàª„ÇãÔºà„Ç®„Éá„Ç£„ÇøÁµåÁî±„Å™„Çâ„Ç®„Éá„Ç£„Çø„Å∏Ôºâ
			if (e.key === "Escape") goBack();
        };

        window.addEventListener("keydown", handleKeyDown);
        return () => window.removeEventListener("keydown", handleKeyDown);
    }, [isPlaying, nextSlide, prevSlide, goBack, branchByNumberKey, hasMultipleBranches, playSound]);

    const effectiveProjectBindings = useMemo(() => {
        const merged = mergeBindings(flow?.projectBindings);
        console.log("PresenterView: effectiveProjectBindings updated", { 
            flowBindings: flow?.projectBindings, 
            merged 
        });
        return merged;
    }, [flow]);

    // --- „Éó„É≠„Ç∏„Çß„ÇØ„ÉàÂÖ®‰Ωì„Éê„Ç§„É≥„Éâ„ÇíÈÅ©Áî®„Åó„Å¶„Ç¢„ÇØ„Ç∑„Éß„É≥ÂÆüË°å ---
    const runAction = useCallback(
        (a: BindingAction) => {
            switch (a.type) {
				case "next":
					nextSlide();
					return;
				case "prev":
					prevSlide();
					return;
				case "branchIndex":
					// 1..9 „Çí ‚ÄúÂàÜÂ≤êÈÅ∏ÊäûÔºàÊï∞Â≠ó„Ç≠„ÉºÔºâ‚Äù „Å®Âêå„ÅòÊåôÂãï„Å´„Åô„Çã
					branchByNumberKey(String(a.index));
					return;
				case "branch": {
					// Êó¢Â≠ò‰∫íÊèõ: A/B/HOME „ÅØ 1..3 „Å´„Éû„ÉÉ„Éó
					if (!hasMultipleBranches) return;
					const map: Record<string, string> = { A: "1", B: "2", HOME: "3" };
					const k = map[a.kind];
					if (k) branchByNumberKey(k);
					return;
				}
				case "reaction":
					// ReactionOverlay „Åå pressed.One/Two „ÇíË¶ã„Å¶„ÅÑ„Çã„ÅÆ„Åß„ÄÅ„Åì„Åì„Åß„ÅØ‰Ωï„ÇÇ„Åó„Å™„ÅÑ
					return;
				case "paint":
					// shouldPaint„ÅßÂà•ÈÄîÂá¶ÁêÜ„Åô„Çã„ÅÆ„Åß„ÄÅ„Åì„Åì„Åß„ÅØ‰Ωï„ÇÇ„Åó„Å™„ÅÑ
					return;
				case "sound":
					// Èü≥Â£∞ÂÜçÁîüÂá¶ÁêÜ
					if (a.kind === "shot") playSound("q");
					else if (a.kind === "oh") playSound("e");
					else if (a.kind === "uxo") playSound("w");
					return;			case "remove":
				// ÊèèÁîª„ÇíÊ∂àÂéª
				setDrawingPoints([]);
				isMouseDrawingRef.current = false;
				wasWiiADownRef.current = false;
				return;				case "none":
				default:
					return;
            }
        },
        [nextSlide, prevSlide, branchByNumberKey, hasMultipleBranches, playSound],
    );

    // ‚òÖ‰øÆÊ≠£: Wii„É™„É¢„Ç≥„É≥„ÅÆ„Éú„Çø„É≥Âá¶ÁêÜÔºàisPlaying„Ååtrue„ÅÆÊôÇ„ÅÆ„ÅøÂãï‰ΩúÔºâ
    const prevPressedRef = useRef<Record<string, boolean>>({});
    useEffect(() => {
        if (!isPlaying) return;

        // „Åù„ÅÆ„Éï„É¨„Éº„É†„Åß„ÄåÊäº„Åï„Çå„ÅüÁû¨Èñì„Äç„ÅÆ„Éú„Çø„É≥„Å†„ÅëÂá¶ÁêÜÔºàÊäº„Åó„Å£„Å±„Å™„Åó„ÅßÈÄ£Êâì„Åó„Å™„ÅÑÔºâ
        const prevPressed = prevPressedRef.current;
        for (const btn of Object.keys(pressed)) {
            const isDown = (pressed as Record<string, boolean>)[btn];
            const wasDown = !!prevPressed[btn];
            if (!isDown || wasDown) continue;

            const act = (effectiveProjectBindings as Record<string, BindingAction | undefined>)[btn] ?? { type: "none" };
            runAction(act);
        }
        prevPressedRef.current = { ...(pressed as Record<string, boolean>) };
    }, [pressed, isPlaying, effectiveProjectBindings, runAction]);

    // ‚òÖËøΩÂä†: „É™„Ç¢„ÇØ„Ç∑„Éß„É≥Ê§úÂá∫Ôºà„Éê„Ç§„É≥„Éâ„Éô„Éº„ÇπÔºâ
    const shouldEmitClap = useMemo(() => {
        if (!isPlaying) return false;
        // Êäº„Åï„Çå„Åü„Éú„Çø„É≥„ÅÆ‰∏≠„Åß„ÄÅ"clap" „Å´„Éê„Ç§„É≥„Éâ„Åï„Çå„Å¶„ÅÑ„Çã„ÇÇ„ÅÆ„Åå„ÅÇ„Çã„ÅãÔºü
        for (const btn of Object.keys(pressed)) {
            const isDown = (pressed as Record<string, boolean>)[btn];
            if (!isDown) continue;
            const act = (effectiveProjectBindings as Record<string, BindingAction | undefined>)[btn];
            if (act?.type === "reaction" && act.kind === "clap") return true;
        }
        return false;
    }, [pressed, effectiveProjectBindings, isPlaying]);

    const shouldEmitLaugh = useMemo(() => {
        if (!isPlaying) return false;
        for (const btn of Object.keys(pressed)) {
            const isDown = (pressed as Record<string, boolean>)[btn];
            if (!isDown) continue;
            const act = (effectiveProjectBindings as Record<string, BindingAction | undefined>)[btn];
            if (act?.type === "reaction" && act.kind === "laugh") return true;
        }
        return false;
    }, [pressed, effectiveProjectBindings, isPlaying]);

    // PAINT„Éú„Çø„É≥„ÅÆÊúÄÂæå„ÅÆÂÖ•ÂäõÊôÇÂàª„ÇíË®òÈå≤
    const lastPaintInputTimeRef = useRef<number>(0);
    const [shouldPaint, setShouldPaint] = useState(false);

    // wiiState.buttons„Çí„ÉÅ„Çß„ÉÉ„ÇØ„Åó„Å¶„ÄÅÁèæÂú®PAINT„Éú„Çø„É≥„ÅåÊäº„Åï„Çå„Å¶„ÅÑ„Çã„ÅãÁ∂ôÁ∂öÁöÑ„Å´Áõ£Ë¶ñ
    useEffect(() => {
        if (!isPlaying || !wiiState) return;

        // ÁèæÂú®Êäº„Åï„Çå„Å¶„ÅÑ„Çã„Éú„Çø„É≥„ÅÆ‰∏≠„Å´PAINT„Åå„ÅÇ„Çã„Åã„ÉÅ„Çß„ÉÉ„ÇØ
        let isPaintButtonPressed = false;
        for (const btn of Object.keys(wiiState.buttons)) {
            const isDown = (wiiState.buttons as Record<string, boolean>)[btn];
            if (!isDown) continue;
            const act = (effectiveProjectBindings as Record<string, BindingAction | undefined>)[btn];
            if (act?.type === "paint") {
                isPaintButtonPressed = true;
                break;
            }
        }

        if (isPaintButtonPressed) {
            lastPaintInputTimeRef.current = Date.now();
            setShouldPaint(true);
        }
    }, [wiiState, effectiveProjectBindings, isPlaying]);

    // 200ms„Çø„Ç§„Éû„Éº„ÅßÊèèÁîªÁä∂ÊÖã„Çí„ÉÅ„Çß„ÉÉ„ÇØ
    useEffect(() => {
        if (!isPlaying) {
            setShouldPaint(false);
            return;
        }

        const interval = setInterval(() => {
            const now = Date.now();
            const elapsed = now - lastPaintInputTimeRef.current;
            
            if (elapsed > 100 && shouldPaint) {
                setShouldPaint(false);
                // ÊèèÁîª„ÇíÁµÇ‰∫Ü
                if (isMouseDrawingRef.current) {
                    isMouseDrawingRef.current = false;
                    setDrawingPoints((prev) => (prev.length > 0 && prev[prev.length - 1] !== null ? [...prev, null] : prev));
                }
            }
        }, 50); // 50ms„Åî„Å®„Å´„ÉÅ„Çß„ÉÉ„ÇØ

        return () => clearInterval(interval);
    }, [isPlaying, shouldPaint]);

    // --- ÊèèÁîª„É≠„Ç∏„ÉÉ„ÇØ (IR„Çª„É≥„Çµ„Éº & PAINT„Éú„Çø„É≥) ---
    useEffect(() => {
        const canvas = canvasRef.current;
        const ctx = canvas?.getContext("2d");
        if (!canvas || !ctx) return;

        // „Ç≠„É£„É≥„Éê„Çπ„Çµ„Ç§„Ç∫„Çí„Ç¶„Ç£„É≥„Éâ„Ç¶„Å´Âêà„Çè„Åõ„Çã
        if (canvas.width !== window.innerWidth || canvas.height !== window.innerHeight) {
            canvas.width = window.innerWidth;
            canvas.height = window.innerHeight;
        }

        // ÁîªÈù¢„ÇØ„É™„Ç¢
        ctx.clearRect(0, 0, canvas.width, canvas.height);

        // Êó¢Â≠ò„ÅÆÁ∑ö„ÇíÊèèÁîª
        ctx.lineWidth = 5;
        ctx.strokeStyle = "red";
        ctx.lineCap = "round";
        ctx.lineJoin = "round";

        if (drawingPoints.length > 1) {
            let started = false;
            for (const p of drawingPoints) {
                if (!p) {
                    if (started) {
                        ctx.stroke();
                        started = false;
                    }
                    continue;
                }
                if (!started) {
                    ctx.beginPath();
                    ctx.moveTo(p.x, p.y);
                    started = true;
                } else {
                    ctx.lineTo(p.x, p.y);
                }
            }
            if (started) ctx.stroke();
        }

        // IR„Éù„Ç§„É≥„Çø„ÉºÂá¶ÁêÜ
        if (wiiState && wiiState.ir.length > 0) {
            // IR„ÅÆ1ÁÇπÁõÆ„Çí‰ΩøÁî®
			const dot = wiiState.ir[0];
			// Â∫ßÊ®ôÂ§âÊèõ
			const pos = mapIrToScreen(dot.x, dot.y, window.innerWidth, window.innerHeight);

			// „Ç´„Éº„ÇΩ„É´ÊèèÁîª
			ctx.fillStyle = "blue";
			ctx.beginPath();
			ctx.arc(pos.x, pos.y, 10, 0, Math.PI * 2);
			ctx.fill();

			// PAINT„Éê„Ç§„É≥„Éâ„Åï„Çå„Åü„Éú„Çø„É≥„ÇíÊäº„Åó„Å¶„ÅÑ„ÇãÈñì„ÄÅËªåË∑°„ÇíËøΩÂä†
			if (shouldPaint) {
				setDrawingPoints((prev) => {
					const next = prev.slice();
					if (!wasWiiADownRef.current) {
						// ÂâçÂõû„ÅÆÁ∑ö„Å®Áπã„Åå„Çâ„Å™„ÅÑ„Çà„ÅÜ„Å´Âå∫Âàá„Çä„ÇíÂÖ•„Çå„Çã
						if (next.length > 0 && next[next.length - 1] !== null) next.push(null);
					}
					next.push(pos);
					return next;
				});
				wasWiiADownRef.current = true;
			} else {
				// Èõ¢„Åó„Åü„Çø„Ç§„Éü„É≥„Ç∞„ÅßÂå∫Âàá„Çã
				if (wasWiiADownRef.current) {
					wasWiiADownRef.current = false;
					setDrawingPoints((prev) => (prev.length > 0 && prev[prev.length - 1] !== null ? [...prev, null] : prev));
				}
			}
        }
    }, [wiiState, drawingPoints, shouldPaint]);


    return (
        <main
            ref={containerRef}
            onMouseDown={(e) => {
                if (!isPlaying) return;
                if (e.button !== 0) return;
                // UI(„Éú„Çø„É≥Á≠â)Êìç‰Ωú„ÅØÈÇ™È≠î„Åó„Å™„ÅÑ
                const el = e.target as HTMLElement | null;
                if (el && el.closest("button, a, input, textarea, select")) return;
                e.preventDefault();
                isMouseDrawingRef.current = true;
                setDrawingPoints((prev) => {
                    const next = prev.slice();
                    if (next.length > 0 && next[next.length - 1] !== null) next.push(null);
                    next.push({ x: e.clientX, y: e.clientY });
                    return next;
                });
            }}
            onMouseMove={(e) => {
                if (!isPlaying) return;
                // PAINT„Éú„Çø„É≥„ÅåÊäº„Åï„Çå„Å¶„ÅÑ„ÇãÂ†¥Âêà„ÇÇÊèèÁîª
                const shouldDrawWithPaint = shouldPaint;
                if (!isMouseDrawingRef.current && !shouldDrawWithPaint) return;
                
                // PAINT„Éú„Çø„É≥„ÅßÊèèÁîªÈñãÂßã
                if (shouldDrawWithPaint && !isMouseDrawingRef.current) {
                    isMouseDrawingRef.current = true;
                    setDrawingPoints((prev) => {
                        const next = prev.slice();
                        if (next.length > 0 && next[next.length - 1] !== null) next.push(null);
                        next.push({ x: e.clientX, y: e.clientY });
                        return next;
                    });
                    return;
                }
                
                e.preventDefault();
                setDrawingPoints((prev) => {
                    const last = prev[prev.length - 1];
                    if (last && Math.abs(last.x - e.clientX) + Math.abs(last.y - e.clientY) < 2) return prev;
                    return [...prev, { x: e.clientX, y: e.clientY }];
                });
            }}
            onMouseUp={() => {
                if (!isMouseDrawingRef.current) return;
                isMouseDrawingRef.current = false;
                setDrawingPoints((prev) => (prev.length > 0 && prev[prev.length - 1] !== null ? [...prev, null] : prev));
            }}
            onMouseLeave={() => {
                if (!isMouseDrawingRef.current) return;
                isMouseDrawingRef.current = false;
                setDrawingPoints((prev) => (prev.length > 0 && prev[prev.length - 1] !== null ? [...prev, null] : prev));
            }}
            style={{
                position: "relative",
                width: "100vw",
                height: "100vh",
                overflow: "hidden",
                background: "black",
            }}
        >
            <WiiDisconnectPopup
                isPlaying={isPlaying}
                startedWithWii={startedWithWii}
                wiiConnected={wiiConnected}
                wiiDisconnectedAt={wiiDisconnectedAt}
                playingSince={playingSince}
            />

            {/* Êàª„Çã„Éú„Çø„É≥ÔºàÂ∑¶‰∏äÔºâ */}
            <div style={{ position: "absolute", top: 20, left: 20, zIndex: 10000 }}>
                <button onClick={goBack} style={{ padding: "10px 14px", fontSize: 14 }}>
                    {returnLabel}
                </button>
            </div>

            {/* „É™„Ç¢„ÇØ„Ç∑„Éß„É≥ */}
            <ReactionOverlay emitClap={shouldEmitClap} emitLaugh={shouldEmitLaugh} />

            {/* „Çπ„É©„Ç§„ÉâË°®Á§∫„Ç®„É™„Ç¢ (ÂÖ®ÁîªÈù¢„Éª‰ΩôÁôΩ„Å™„Åó„Éª„Ç¢„Çπ„Éö„ÇØ„ÉàÊØîÁ∂≠ÊåÅ) */}
            <div style={{ position: "absolute", inset: 0, display: "flex", alignItems: "center", justifyContent: "center" }}>
                {currentNode ? (
                    <>
                        {currentNode.data.asset?.kind === "pdf" ? (
                            <PdfSlide
                                assetId={currentNode.data.asset.assetId}
                                page={currentNode.data.asset.page ?? 1}
                                fallbackDataUrl={currentNode.data.asset.thumbnailDataUrl}
                                alt={currentNode.data.label}
                                getOrLoadPdfDocument={getOrLoadPdfDocument}
                            />
                        ) : currentNode.data.asset?.kind === "video" ? (
                            <VideoSlide assetId={currentNode.data.asset.assetId} alt={currentNode.data.label} />
                        ) : (
                            <h1 style={{ fontSize: 80, color: "white", textAlign: "center", maxWidth: "80%" }}>
                                {currentNode.data.label}
                            </h1>
                        )}
                    </>
                ) : (
                    <div style={{ color: "white" }}>{error ?? "„Çπ„É©„Ç§„Éâ„Éá„Éº„Çø„Åå„ÅÇ„Çä„Åæ„Åõ„Çì"}</div>
                )}
            </div>

            {/* ÊèèÁîª„É¨„Ç§„É§„Éº (ÊúÄÂâçÈù¢) */}
            <canvas
                ref={canvasRef}
                style={{ position: "absolute", top: 0, left: 0, pointerEvents: "none" }}
            />

            {/* „Éá„Éê„ÉÉ„Ç∞ÊÉÖÂ†± (Âè≥‰∏ä) */}
            <WiiDebugPanel
                wiiState={wiiState}
                pressed={pressed}
                effectiveProjectBindings={effectiveProjectBindings}
            />

            {/* Êìç‰Ωú„Ç¨„Ç§„Éâ (Â∑¶‰∏ã) */}
            <div style={{ position: "absolute", bottom: 20, left: 20, color: "rgba(255,255,255,0.5)", fontSize: 14, pointerEvents: "none" }}>
                [ESC] Êàª„Çã
            </div>
        </main>
    );
}